/* SMOOTHER GRADIENT BACKGROUND */
body {
  background: linear-gradient(120deg, #ffd6e8, #ffe6f2, #ffcce0);
  background-size: 400% 400%;
  animation: bgMove 14s ease-in-out infinite;
  font-family: "Comic Sans MS", "Poppins", sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
}

@keyframes bgMove {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* CARD WITH ULTRA-SMOOTH TRANSITIONS */
.card {
  text-align: center;
  padding: 30px;
  width: 350px;
  border-radius: 22px;
  animation: fadeIn 1.2s ease-out;
  transition:
    background 0.9s ease-in-out,
    box-shadow 0.9s ease-in-out,
    transform 0.6s ease-in-out,
    opacity 0.6s ease-in-out;
}

/* FADE-OUT WHEN SWITCHING THEMES */
.card.fade {
  opacity: 0;
  transform: scale(0.96);
}

.card:hover {
  transform: translateY(-4px);
}

/* THEMES */
.dudu-theme {
  background: #fff3e0;
  box-shadow: 0 0 35px rgba(149, 92, 0, 0.25);
}

.dudu-theme .title {
  color: #8a5a20;
}

.dudu-theme .btn {
  background: #8a5a20;
}

.dudu-theme .btn:hover {
  background: #704716;
}

.bubu-theme {
  background: #ffe6f2;
  box-shadow: 0 0 35px rgba(255, 100, 150, 0.4);
}

.bubu-theme .title {
  color: #ff4d88;
}

.bubu-theme .btn {
  background: #ff4d88;
}

.bubu-theme .btn:hover {
  background: #ff1a66;
}

/* BEAR IMAGE â€” CROSS FADE */
.bear {
  width: 200px;
  border-radius: 15px;
  transition: opacity 0.55s ease-in-out;
  opacity: 1;
}

.bear.fade-out {
  opacity: 0;
}

/* BUTTON */
.btn {
  margin-top: 20px;
  padding: 12px 20px;
  color: white;
  border: none;
  border-radius: 12px;
  cursor: pointer;
  font-size: 16px;
  transition: background 0.4s ease, transform 0.25s ease;
}

.btn:hover {
  transform: scale(1.06);
}

/* FLOATING HEARTS */
.hearts {
  font-size: 32px;
  margin-top: 20px;
  animation: float 2.8s infinite ease-in-out;
}

@keyframes float {
  0% { transform: translateY(0); opacity: 0.9; }
  50% { transform: translateY(-15px); opacity: 1; }
  100% { transform: translateY(0); opacity: 0.9; }
}

/* SPARKLES */
.effects {
  position: relative;
}

.sparkles {
  pointer-events: none;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  opacity: 0.4;
  background: radial-gradient(circle, rgba(255,255,255,0.9) 20%, transparent 55%);
  background-size: 12px 12px;
  animation: sparkle 3.5s infinite ease-in-out;
  transition: opacity 1s ease-in-out;
}

@keyframes sparkle {
  0% { opacity: 0.15; }
  50% { opacity: 0.5; }
  100% { opacity: 0.15; }
}

/* SMOOTH CARD FADE-IN */
@keyframes fadeIn {
  0% { opacity: 0; transform: translateY(15px); }
  100% { opacity: 1; transform: translateY(0); }
}